.solutions {
	position: relative;

	&__title {
		font-weight: 700;
		font-size: 26px;
		line-height: 1.2;
		margin-bottom: 18px;
	}

	&__wrapper {
		position: relative;
	}

	&__nav {
		position: absolute;
		top: 120px;
		left: -25px;
		z-index: 3;
		width: calc(100% + 50px);
	}
}