body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
ol,
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
}

html {
  font-size: 16px;
  line-height: 1.2;
  letter-spacing: -0.03em;
  font-weight: 400;
  -moz-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  -o-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  text-size-adjust: 100%;
  scroll-behavior: smooth;
}

body {
  min-height: 100vh;
  scroll-behavior: smooth;
  text-rendering: optimizeSpeed;
  font-family: "Univers", sans-serif;
  background-color: #00141f;
  color: #ffffff;
  /* iOS fix */
  -webkit-font-smoothing: antialiased;
  -moz-font-smoothing: antialiased;
  -o-font-smoothing: antialiased;
  -webkit-locale: auto;
  white-space: normal;
}

main {
  position: relative;
  z-index: 1;
}

a {
  color: #ffffff;
  text-decoration: none;
}

ul,
ol,
nav {
  list-style: none;
  padding: 0;
}

svg {
  width: auto;
  height: auto;
}

img {
  display: block;
  max-width: 100%;
}

input,
button {
  -webkit-appearance: none;
}

input,
button,
select,
textarea {
  font: inherit;
  background-color: transparent;
  border: 0;
  border-radius: 0;
  outline: 0;
}

input,
textarea {
  padding: 0;
  margin: 0;
}
input:focus::placeholder,
textarea:focus::placeholder {
  opacity: 0;
}

.d-none-desktop {
  display: block !important;
}
@media only screen and (min-width: 768px) {
  .d-none-desktop {
    display: none !important;
  }
}

.d-none-mobile {
  display: none !important;
}
@media only screen and (min-width: 768px) {
  .d-none-mobile {
    display: block !important;
  }
}

/* Swiper */
.swiper-nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 20px;
  width: 100%;
}

.swiper-btn-prev,
.swiper-btn-next {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background-color: rgba(0, 104, 183, 0.63);
  cursor: pointer;
  transition: background-color 0.3s ease;
}
.swiper-btn-prev:hover,
.swiper-btn-next:hover {
  background-color: rgba(0, 104, 183, 0.5);
}
.swiper-btn-prev svg,
.swiper-btn-next svg {
  color: #ffffff;
}

/* Blocks */
.container {
  max-width: 990px;
  width: 100%;
  padding: 0 20px;
  margin: 0 auto;
  box-sizing: border-box;
}

.button {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  background-color: #f39800;
  padding: 12px 14px;
  box-shadow: 0 7px 30px rgba(243, 152, 0, 0.69);
  border-radius: 36px;
  color: #ffffff;
  font-weight: 700;
  font-size: 14px;
  line-height: 1.2;
  cursor: pointer;
  transition: 0.3s ease;
}
.button:hover {
  background-color: rgba(243, 152, 0, 0.8);
}

.header {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 10;
  padding: 48px 0 20px;
}
.header__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 20px;
}
.header__logo svg, .header__logo-cn svg {
  color: #ffffff;
}
.header__logo svg {
  width: 164px;
  height: 48px;
}
.header__logo-cn svg {
  width: 102px;
  height: 29px;
}

.solution-card {
  position: relative;
}
.solution-card__image {
  width: 100%;
  height: 287px;
  border-radius: 32px;
  overflow: hidden;
}
.solution-card__image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: 0.3s ease;
}
.solution-card__title {
  color: #41bbdd;
  font-weight: 700;
  font-size: 20px;
  line-height: 1.2;
  margin-top: 13px;
}
.solution-card__text {
  font-size: 14px;
  line-height: 1.2;
  margin-top: 11px;
}
.solution-card__button {
  box-shadow: none;
  padding: 12px 42px;
  margin-top: 20px;
}
.solution-card__link {
  position: absolute;
  inset: 0;
  z-index: 1;
}
.solution-card__link:hover + .solution-card__image img {
  transform: scale(1.05);
}

.article {
  display: flex;
  flex-direction: column;
  padding: 21px 24px 25px;
  background-color: rgba(0, 104, 183, 0.73);
  border-radius: 32px;
}
.article__title {
  font-weight: 700;
  font-size: 26px;
  line-height: 1.2;
}
.article__text {
  font-size: 16px;
  line-height: 1.2;
  margin-top: 15px;
}
.article__link {
  margin-top: 33px;
  margin-left: auto;
}

.advantages {
  display: flex;
  align-items: center;
  gap: 12px;
}
.advantages__icon {
  display: flex;
  align-items: center;
  justify-content: center;
  max-width: 60px;
  width: 100%;
  height: 60px;
  padding: 14px;
  background-color: #f39800;
  border-radius: 50%;
  box-shadow: 0 7px 31px rgba(243, 152, 0, 0.69);
  overflow: hidden;
}
.advantages__icon svg {
  color: #ffffff;
}
.advantages__title {
  font-size: 16px;
  line-height: 1.2;
}
.advantages__text {
  font-size: 12px;
  line-height: 1.2;
  letter-spacing: -0.04em;
}

.hero {
  position: relative;
  padding: 153px 0 20px;
}
.hero__bg {
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
}
.hero__bg img {
  width: 100%;
  object-fit: cover;
}
.hero__container {
  display: flex;
  justify-content: space-between;
  gap: 50px;
}
.hero__left {
  max-width: 474px;
  width: 100%;
}
.hero__advantages {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  gap: 47px;
  max-width: 290px;
  width: 100%;
  padding-top: 9px;
}
.hero__title {
  font-weight: 700;
  font-size: 55px;
  line-height: 1.2;
  text-transform: uppercase;
}
.hero__title span {
  display: inline-block;
  background-color: #f39800;
}
.hero__description {
  font-size: 16px;
  line-height: 1.2;
  margin-top: 30px;
}

.about {
  padding: 30px 0 40px;
}
.about__container {
  display: flex;
  justify-content: space-between;
  gap: 70px;
}
.about__left {
  min-width: 403px;
  max-width: 403px;
}
.about__right {
  width: 100%;
}
.about__articles {
  display: flex;
  flex-direction: column;
  gap: 44px;
}
.about__solutions {
  margin-top: 51px;
}
.about__accordion + .about__accordion {
  margin-top: 97px;
}
.about__accordion:nth-child(2) {
  margin-top: 8px;
}
.about__materials {
  margin-top: 74px;
}

.accordion {
  display: flex;
  flex-direction: column;
}
.accordion__tab-wrapper {
  position: relative;
  display: flex;
  justify-content: flex-end;
}
.accordion__tab-wrapper::before, .accordion__tab-wrapper::after {
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  content: "";
}
.accordion__tab-wrapper::before {
  max-width: calc(100% - 212px);
  width: 100%;
  height: 2px;
  background-image: linear-gradient(to right, #fff 33%, rgba(255, 255, 255, 0) 0%);
  background-position: bottom;
  background-size: 8px 2px;
  background-repeat: repeat-x;
}
.accordion__tab-wrapper::after {
  width: 3px;
  height: 13px;
  background-color: #ffffff;
}
.accordion__tab {
  display: inline-flex;
  align-items: center;
  gap: 8px;
  max-width: 212px;
  width: 100%;
  padding: 8px 8px 8px 12px;
  border-radius: 14px;
  background-color: rgba(0, 104, 183, 0.63);
  font-weight: 700;
  font-size: 16px;
  line-height: 1.2;
  cursor: pointer;
}
.accordion__tab svg {
  color: #f39800;
}
.accordion__content {
  display: none;
  padding: 24px 24px 22px 30px;
  background-color: rgba(255, 255, 255, 0.73);
  border-radius: 14px;
  font-size: 16px;
  line-height: 1.2;
  color: #0068b7;
  margin-top: 5px;
}

.solutions {
  position: relative;
}
.solutions__title {
  font-weight: 700;
  font-size: 26px;
  line-height: 1.2;
  margin-bottom: 18px;
}
.solutions__wrapper {
  position: relative;
}
.solutions__nav {
  position: absolute;
  top: 120px;
  left: -25px;
  z-index: 3;
  width: calc(100% + 50px);
}

.materials {
  position: relative;
}
.materials__title {
  padding: 0 27px;
  font-weight: 700;
  font-size: 20px;
  line-height: 1.2;
  color: #41bbdd;
}
.materials__content {
  display: flex;
  gap: 45px;
  padding: 25px 27px 22px;
  border-radius: 32px;
  border: 1px solid #41bbdd;
  margin-top: 15px;
}
.materials__text {
  max-width: 200px;
  font-size: 12px;
  line-height: 1.2;
}
.materials__plate {
  position: relative;
  margin-top: -88px;
}
.materials__plate::before {
  position: absolute;
  content: "";
  display: block;
  width: 98px;
  height: 138px;
  border-radius: 5px;
  background-color: #d9d9d9;
  transform: translate3d(64px, 2px, 0) rotate(17deg);
}
.materials__plate::after {
  position: absolute;
  width: 98px;
  height: 138px;
  content: "";
  border-radius: 5px;
  background-color: #ffffff;
  transform: rotate(9deg);
}
.materials__download {
  position: absolute;
  bottom: -20px;
  right: 23px;
  padding: 12px 52px;
}